import React, { useCallback } from "react";
import CartButtons from "../../components/CartButtons.jsx/CartButtons";
import { useCurrencyStore } from "../../store/currencyStore";
import { useShoppingCartStore } from "../../store/shoopingCartStore";
import { fixedPrice, getOccurrence } from "../../utils";
import { ProductBlock } from "./Gallery.style";

const GalleryProduct = ({
  isAvailible,
  qtt 
  ,
  id,
  title,
  src,
  details,
  price,
}) => {
  const { currency } = useCurrencyStore();
  const ratedPrice = fixedPrice(currency?.rate * price);

  //fucc for cart buttons
  const { removeFromCart, addToCart, sc } = useShoppingCartStore();
  const addMemoized = useCallback(addToCart, []);
  const removeMemoized = useCallback(removeFromCart, []);

  const amount = getOccurrence(sc, id);
  return (
    <ProductBlock isAvailible={isAvailible}>
      <h2>{title}</h2>
      <img src={src} alt={title} />
      <p>{details}</p>
      <div>
        {currency?.symbol}
        {ratedPrice}
      </div>
      {isAvailible && (
        <CartButtons
          id={id}
          addIsDisabled = {amount >= qtt}

          price={price}
          addToCart={addMemoized}
          removeFromCart={removeMemoized}
          removeIsDisabled={amount < 1}
        />
      )}
      {!isAvailible && <div className="notAvailibleSticker">NOT AVAILIBLE</div>}
    </ProductBlock>
  );
};
export default GalleryProduct;
